import uniPopup from "@/components/uni-popup/uni-popup.vue"
export default {
	data() {
		return {
			nickName:"",
			avatarUrl:"",
			orderList:[
				{
					tid:1,
					t_name:"待付款",
					t_img:"../../static/img/dfk.png",
				},{
					tid:2,
					t_name:"待发货",
					t_img:"../../static/img/dfh.png"
				},{
					tid:3,
					t_name:"待收货",
					t_img:"../../static/img/dss.png"
				},{
					tid:4,
					t_name:"退货",
					t_img:"../../static/img/th.png"
				},{
					tid:0,
					t_name:"我的订单",
					t_img:"../../static/img/mydd.png"
				}
			]
		}
	},
	components: {
		uniPopup,
	},
	onLoad(){
		this.pageInit();
	},
	methods: {
		pageInit(){
			this.nickName = uni.getStorageSync("nickName");
			this.avatarUrl = uni.getStorageSync("avatarUrl");
		},
		toOrder(tid){
			uni.navigateTo({
				url: '../order/order?tid='+tid
			});
		},
		toAddress() {
			uni.navigateTo({
				url: '../address/address'
			});
		},
		clickLogin() {
			this.$refs.popup.open();
		},
		cancel(){
			this.$refs.popup.close();
		},
		wxGetUserInfo() {
			let _this = this;
			wx.getSetting({
			  success (res){
				if (res.authSetting['scope.userInfo']) {
				  // 已经授权，可以直接调用 getUserInfo 获取头像昵称
				  wx.getUserInfo({
					success: function(res) {
						let userInfo =  res.userInfo;
						uni.setStorageSync('nickName', userInfo.nickName);
						uni.setStorageSync('avatarUrl', userInfo.avatarUrl);
						_this.$refs.popup.close();
						_this.pageInit();
					}
				  })
				}
			  }
			})
		}
	}
}
